

import { h } from 'preact';
import { useState } from 'preact/hooks';
import { __ } from '@wordpress/i18n';
import { BaseAdminBlockComponent } from './BlockComponent.jsx';

const ProductList = (props) => {
	const component = new BaseAdminBlockComponent(props);
	const [products, setProducts] = useState(component.value || []);

	const add_product = () => {
		const newProducts = [...products, {
			product_id: '',
			quantity: 1,
			price: 0
		}];
		setProducts(newProducts);
		component.handle_change(newProducts);
	};

	const remove_product = (index) => {
		const newProducts = products.filter((_, i) => i !== index);
		setProducts(newProducts);
		component.handle_change(newProducts);
	};

	const update_product = (index, field, value) => {
		const newProducts = [...products];
		newProducts[index] = {
			...newProducts[index],
			[field]: value
		};
		setProducts(newProducts);
		component.handle_change(newProducts);
	};

	component.render_input = () => {
		const isGrid = component.attrs.display_mode === 'grid';

		return (
			<div className={`product-list-container ${isGrid ? 'grid-mode' : 'list-mode'}`}>
				<div className={isGrid ? 'grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4' : 'space-y-2'}>
					{products.length === 0 ? (
						<div className="text-gray-500 text-sm py-4">
							{__('No products added yet.', 'wpcbooking')}
						</div>
					) : (
						products.map((product, index) => (
							<div 
								key={index}
								className={`product-item ${isGrid ? 'border border-gray-300 rounded p-4' : 'flex items-center gap-3 border-b border-gray-200 pb-2'}`}
							>
								<div className={`${isGrid ? 'space-y-3' : 'flex-1 flex gap-3 items-center'}`}>
									<div className={isGrid ? '' : 'flex-1'}>
										<input
											type="text"
											placeholder={__('Product ID', 'wpcbooking')}
											value={product.product_id}
											className="w-full px-3 py-2 border border-gray-300 rounded bg-white focus:outline-none focus:ring-2 focus:ring-blue-500"
											onInput={(e) => update_product(index, 'product_id', e.target.value)}
										/>
									</div>
									<div className={isGrid ? '' : 'w-24'}>
										<input
											type="number"
											placeholder={__('Qty', 'wpcbooking')}
											value={product.quantity}
											min="1"
											className="w-full px-3 py-2 border border-gray-300 rounded bg-white focus:outline-none focus:ring-2 focus:ring-blue-500"
											onInput={(e) => update_product(index, 'quantity', parseInt(e.target.value) || 1)}
										/>
									</div>
									<div className={isGrid ? '' : 'w-32'}>
										<input
											type="number"
											placeholder={__('Price', 'wpcbooking')}
											value={product.price}
											min="0"
											step="0.01"
											className="w-full px-3 py-2 border border-gray-300 rounded bg-white focus:outline-none focus:ring-2 focus:ring-blue-500"
											onInput={(e) => update_product(index, 'price', parseFloat(e.target.value) || 0)}
										/>
									</div>
								</div>
								<button
									type="button"
									onClick={() => remove_product(index)}
									className="p-2 text-red-500 hover:text-red-700 hover:bg-red-50 rounded"
									title={__('Remove product', 'wpcbooking')}
								>
									<svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" className="w-5 h-5">
										<path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
									</svg>
								</button>
							</div>
						))
					)}
				</div>

				<div className="mt-4 flex justify-end">
					<button
						type="button"
						onClick={add_product}
						className="flex items-center gap-2 px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded transition-colors"
					>
						<svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" className="w-5 h-5">
							<path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 4v16m8-8H4" />
						</svg>
						<span>{__('Add product', 'wpcbooking')}</span>
					</button>
				</div>
			</div>
		);
	};

	component.get_default_label = () => __('Products', 'wpcbooking');

	return component.render();
};

export default ProductList;


